import React, { useState, useEffect } from "react";
import { Provider, darkTheme, lightTheme, Button, TextField, View, Heading } from "@adobe/react-spectrum";
import axios from "axios";

export default function Converter() {
    const [input, setInput] = useState("");
    const [output, setOutput] = useState("");
    const [error, setError] = useState("");
    const [darkMode, setDarkMode] = useState(false);

    // Apply/remove dark mode class on body
    useEffect(() => {
        if (darkMode) {
            document.body.classList.add("dark-mode");
        } else {
            document.body.classList.remove("dark-mode");
        }
    }, [darkMode]);

    const convertNumber = async () => {
        setError(""); // Clear previous errors
        setOutput(""); // Clear previous output

        if (!input || isNaN(input)) {
            setError("Please enter a valid number.");
            return;
        }

        try {
            const response = await axios.get(`http://localhost:8080/romannumeral?query=${input}`);
            setOutput(response.data.output);
        } catch (err) {
            setError("Invalid input! Number must be between 1 and 3999.");
        }
    };

    return (
        <Provider theme={darkMode ? darkTheme : lightTheme}>
            <View padding="size-200" maxWidth="size-3600">
                <Heading level={1}>Roman Numeral Converter</Heading>

                <TextField
                    label="Enter a Number"
                    value={input}
                    onChange={setInput}
                    width="size-3000"
                />

                <Button variant="primary" onPress={convertNumber} marginY="size-200">
                    Convert
                </Button>

                {error && <View padding="size-100" backgroundColor="negative">{error}</View>}
                {output && <View padding="size-100" backgroundColor="positive">Roman Numeral: {output}</View>}

                <Button variant="secondary" onPress={() => setDarkMode(prevMode => !prevMode)} marginY="size-200">
                    Toggle {darkMode ? "Light" : "Dark"} Mode
                </Button>
            </View>
        </Provider>
    );
}
